import { Routes } from '@angular/router';

export const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    redirectTo: 'home',
  },
  {
    path: 'home',
    title: 'Home',
    data: {
      headerHeight: '100vh',
    },
    loadComponent: () => import('./pages/landing/landing.component').then(x => x.LandingComponent),
  },
  {
    path: 'form/reg',
    title: 'Register',
    data: {
      headerHeight: '30vh',
    },
    loadComponent: () => import('./forms/reg-bengkel/reg-bengkel.component').then(x => x.RegBengkelComponent),
  },
  {
    path: 'items',
    title: 'Store',
    data: {
      headerHeight: '30vh',
    },
    loadComponent: () => import('./pages/list-items/list-items.component').then(x => x.ListItemsComponent),
  },
  {
    path: 'about',
    title: 'About',
    data: {
      headerHeight: '30vh',
    },
    loadComponent: () => import('./pages/about/about.component').then(x => x.AboutComponent),
  },
];
